import React, { Component } from 'react';
import Particles from "react-tsparticles";
import './App.css';
import AboutMe from './components/aboutMe/aboutMe';
import Home from './components/Home/Home';
import Education from './components/Education/Education';
import Experience from './components/Experience/Experience';
import Project from './components/Project/Project';
import OpenSource from './components/OpenSource/OpenSource';
import ContactMe from './components/ContactMe/ContactMe';
import Navigation from './components/Navigation/Navigation';
import Thanks from './components/Thanks/Thanks';
import DarkMode from './components/DarkMode/DarkMode';


class App extends Component {
  constructor() {
    super();
    this.state = {
      route: 'intro',
      color: 'dark'
    }
  }
  onRouteChange = (route) => {
    this.setState({ route: route });

  }
  ToggleDarkMode=()=>{
    this.setState({color:'dark'});
  }
  ToggleLightMode=()=>{
    this.setState({color:'light'});
  } 
  render() {
    const { route, color } = this.state;
    return (
       
      <div>
        
        {/* <input type='radio' name='group' onClick={this.ToggleLightMode}></input>
        <input type='radio' name='group' onClick={this.ToggleDarkMode}></input> */}
        {color === 'dark' ?
          <div id='dark' className="App" >
            
            <Particles
              className='Particles'
              id="tsparticles"
              options={{

                fpsLimit: 60,
                interactivity: {
                  detectsOn: "canvas",
                  events: {
                    onClick: {
                      enable: true,
                      mode: "push",
                    },
                    onHover: {
                      enable: true,
                      mode: "repulse",
                    },
                    resize: true,
                  },
                  modes: {
                    bubble: {
                      distance: 400,
                      duration: 2,
                      opacity: 0.8,
                      size: 40,
                    },
                    push: {
                      quantity: 4,
                    },
                    repulse: {
                      distance: 200,
                      duration: 0.4,
                    },
                  },
                },
                particles: {
                  color: {
                    value: "#ffffff",
                  },
                  links: {
                    color: "#ffffff",
                    distance: 150,
                    enable: true,
                    opacity: 0.5,
                    width: 1,
                  },
                  collisions: {
                    enable: true,
                  },
                  move: {
                    direction: "none",
                    enable: true,
                    outMode: "bounce",
                    random: false,
                    speed: 2,
                    straight: false,
                  },
                  number: {
                    density: {
                      enable: true,
                      value_area: 800,
                    },
                    value: 80,
                  },
                  opacity: {
                    value: 0.5,
                  },
                  shape: {
                    type: "circle",
                  },
                  size: {
                    random: true,
                    value: 5,
                  },
                },
                detectRetina: true,
              }}
            />
            {(route === 'intro') ?
              <AboutMe onRouteChange={this.onRouteChange} />
              :

              <div className='change'>
                <Navigation onRouteChange={this.onRouteChange} />
                <p className='tl ma0 f6 white b'>LIGHT MODE</p>
                <div class="switch-field">
		              <input onClick={this.ToggleLightMode} type="radio" id="radio-one" name="switch-one" value="yes" checked/>
		              <label for="radio-one">Yes</label>
		              <input onClick={this.ToggleDarkMode} type="radio" id="radio-two" name="switch-one" value="no" />
		              <label for="radio-two">No</label>
	              </div>
                {route === 'home' ?
                  <Home />
                  : (route === 'education' ?
                    <Education />
                    : (route === 'experience' ?
                      <Experience />
                      : (route === 'project' ?
                        <Project />
                        : (route === 'opensource' ?
                          <OpenSource />
                          :
                          <ContactMe />)))
                  )

                }
                <Thanks onRouteChange={this.onRouteChange} />

              </div>
              }
            </div>
            :
            <div id='light' className="App" >
              <Particles
                className='Particles'
              id="tsparticles"
              options={{

                fpsLimit: 60,
                interactivity: {
                  detectsOn: "canvas",
                  events: {
                    onClick: {
                      enable: true,
                      mode: "push",
                    },
                    onHover: {
                      enable: true,
                      mode: "repulse",
                    },
                    resize: true,
                  },
                  modes: {
                    bubble: {
                      distance: 400,
                      duration: 2,
                      opacity: 0.8,
                      size: 40,
                    },
                    push: {
                      quantity: 4,
                    },
                    repulse: {
                      distance: 200,
                      duration: 0.4,
                    },
                  },
                },
                particles: {
                  color: {
                    value: "#ffffff",
                  },
                  links: {
                    color: "#ffffff",
                    distance: 150,
                    enable: true,
                    opacity: 0.5,
                    width: 1,
                  },
                  collisions: {
                    enable: true,
                  },
                  move: {
                    direction: "none",
                    enable: true,
                    outMode: "bounce",
                    random: false,
                    speed: 2,
                    straight: false,
                  },
                  number: {
                    density: {
                      enable: true,
                      value_area: 800,
                    },
                    value: 80,
                  },
                  opacity: {
                    value: 0.5,
                  },
                  shape: {
                    type: "circle",
                  },
                  size: {
                    random: true,
                    value: 5,
                  },
                },
                detectRetina: true,
               }}
              />
             {(route === 'intro') ?
              <AboutMe onRouteChange={this.onRouteChange} />
              :

              <div className='change'>
                <Navigation onRouteChange={this.onRouteChange} />
                <p className='tl ma0 f6 white b'>LIGHT MODE</p>
                <div class="switch-field">
		              <input onClick={this.ToggleLightMode} type="radio" id="radio-one" name="switch-one" value="yes" checked/>
		              <label for="radio-one">Yes</label>
		              <input onClick={this.ToggleDarkMode} type="radio" id="radio-two" name="switch-one" value="no" />
		              <label for="radio-two">No</label>
	              </div>

                {route === 'home' ?
                  <Home />
                  : (route === 'education' ?
                    <Education />
                    : (route === 'experience' ?
                      <Experience />
                      : (route === 'project' ?
                        <Project />
                        : (route === 'opensource' ?
                          <OpenSource />
                          :
                          <ContactMe />)))
                  )

                }
                <Thanks onRouteChange={this.onRouteChange} />

              </div>
              }
            </div>

        }
      </div>
    );
  }
}

export default App;
