import React from "react";
import Tilt from 'react-tilt';
import './aboutMe.css';

const AboutMe  = ({onRouteChange}) =>{
    return(
        <div className='TiltParent' >
          
          <Tilt className="Tilt" options={{ max : 25 }} >
               <div className="Tilt-inner br2 shadow-2 ba bw3 br-4 " onClick={()=>onRouteChange('home')}>  
                  <p className='f1 black grow pointer'> A website </p>
                  <p className='f6 black grow pointer'>by </p>
                  <p className = 'f1 black grow pointer'>Aaron</p>
               </div>
          </Tilt>
            
        </div>
    );
}

export default AboutMe;