import React from "react";

 const Navigation  = ({onRouteChange}) =>{
    return(
        <div  >
          <div style={{display:'flex', justifyContent:'flex-end'}}>
            <p onClick={()=>onRouteChange('home')} className='p2 ph2 link dim white b pointer' >About Me</p>
            <p onClick={()=>onRouteChange('education')} className='p2 ph2 link dim white b pointer' >Education</p>
            <p onClick={()=>onRouteChange('experience')} className='p2 ph2 link dim white b pointer' >Experience</p>
            <p onClick={()=>onRouteChange('opensource')} className='p2 ph2 link dim white b pointer' >Skill</p>
            <p onClick={()=>onRouteChange('project')} className='p2 ph2 link dim white b pointer' >Project</p>
            <p onClick={()=>onRouteChange('contactme')} className='p2 ph2 link dim white b pointer' >Contact Me</p>
          </div>  
          <hr></hr>
        </div>
    );
}

export default Navigation;